import React from 'react';
import UserService, { UserStatusMsg } from '../api/UserService';

interface NProps{}
interface NState{
    userStatus: UserStatusMsg;
}

interface GProps{
    onClick: (event: React.MouseEvent<HTMLButtonElement>) => void;
}

const LogoutBtn:React.FC<{props:GProps}> = ({props}) => {
        return (
            <div className="w3-bar-item w3-right">
                <button onClick={props.onClick}>登出</button>
            </div>
        )
}

const LoginBtn:React.FC<{props:GProps}> = ({props}) => {
    return(
        <div className="w3-bar-item w3-right">
            <button onClick={props.onClick}>登入</button>
        </div>   
    );
}

const RegisterBtn:React.FC<{props:GProps}> = ({props}) => {
    return(
        <div className="w3-bar-item w3-right">
            <button onClick={props.onClick}>註冊</button>
        </div>   
    );
}

class Navbar extends React.Component<NProps, NState> {
    private _userService: UserService;

    constructor(props: NProps){
        super(props);
        this._userService = new UserService();
        this.state = {
            userStatus:{
                message:'',
                success:false,
                name:''
            }
        }
    }

    home = ():void => {
        window.location.href = '/';
    }

    register = ():void => {
        window.location.href = '/register';
    }

    login = ():void => {
        window.location.href = '/login';
    }

    logout = ():void => {
        this._userService.logout();
        window.location.href = '/';
    }

    isLogin = async() => {
        let status = await this._userService.isLogin();
        this.setState({userStatus: status});
    }

    componentDidMount(){
        this.isLogin();
    }
    
    render() {
        let statusBtn = () => {
            if(this.state.userStatus.success){
                let logoutHandler:GProps = {onClick: this.logout}               
                return <LogoutBtn props={logoutHandler}/>
            }else{
                let loginHandler:GProps = {onClick: this.login}  
                let registerHandler: GProps = {onClick: this.register}             
                return (
                    <>
                        <LoginBtn props={loginHandler} />
                        <RegisterBtn props={registerHandler} />
                    </>
                );
            }
        }

        return (
            <div className="w3-top">
                <div className="w3-bar w3-white w3-wide w3-padding w3-card">
                    <div className="w3-bar-item w3-button" onClick={this.home}>
                        <img src="/images/logo.png" width="30" height="20" alt="no pic" style={{width:"auto"}}></img>
                        <b>You</b>Tobe
                    </div>
                    {statusBtn()}
                    <div className="w3-bar-item w3-right">
                        <label htmlFor="user">{this.state.userStatus.name}</label>
                    </div>                   
                </div>
            </div>
        )
    }
}

export default Navbar;