import React from 'react';
import './form.css';
import UserService from '../api/UserService';

interface LProps {}
interface LState {
    account?: string;
    password?: string;
    uname?:string;
}

class Login extends React.Component<LProps, LState> {
    private userService: UserService;

    constructor(props: LProps){
        super(props);
        this.userService = new UserService();
        this.state = {
            account: '',
            password: '',
            uname:''
        }
    } 

    handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        e.preventDefault()
        this.setState({[e.target.name]: e.target.value}) //html tag <name="name" value="value">
    }

    submitInfo = async() => {
        let data = this.state;
        let msg = await this.userService.login(data);
        if(!msg.success)
            this.setState({uname:'帳號或密碼錯誤!'});
        else
            window.location.href = '/';
    }

    render (){
        return (
            <div className="loginContainer">
                <div className="loginFormContainer">
                    <hr></hr>
                    <label htmlFor="account"><b>帳號</b></label>
                    <input type="text" placeholder="帳號" name="account" value={this.state.account} onChange={this.handleChange} required></input>

                    <label htmlFor="psw"><b>密碼</b></label>
                    <input type="password" placeholder="密碼" name="password" value={this.state.password} onChange={this.handleChange} required></input>

                    <label style={{color:"red"}}>{this.state.uname}</label>
                    <hr></hr>
                    <button className="registerbtn" onClick={this.submitInfo} >登入</button>
                </div>
                <div className="signin">
                    <p>還沒加入會員? <a href="/register">快去註冊</a>.</p>
                </div>
            </div>
        );
    }
}

export default Login
