import React from 'react';
declare var videojs: any;

interface SProps{}

class Streaming extends React.Component<SProps> {
    private player: React.RefObject<any>
    private videoPlayer: any

    constructor(props: SProps){
        super(props);
        this.player = React.createRef();
        this.videoPlayer = undefined;
    }

    componentDidMount(){
        var player = this.player.current;
        this.videoPlayer = videojs(player);
        this.videoPlayer.controlBar.addChild('QualitySelector');
        this.videoPlayer.ready(() => {
            this.videoPlayer.src([
                {
                    src: '/dash/big_1080/big.mpd',
                    type: 'application/dash+xml',
                    label: '1080P'
                },
                {
                    src: '/dash/big_720/big.mpd',
                    type: 'application/dash+xml',
                    label: '720P',
                },
                {
                    src: '/dash/big_480/big.mpd',
                    type: 'application/dash+xml',
                    label: '480P',
                    selected: 'true'
                },
                {
                    src: '/dash/big_360/big.mpd',
                    type: 'application/dash+xml',
                    label: '360P'
                }
            ]);
            this.videoPlayer.play();
        });
    }

    componentWillUnmount(){
        if (this.videoPlayer) {
            this.videoPlayer.dispose();
        }
    }

    render() {
        const style = {
            maxWidth:"1200px",
            marginTop:"100px"
        };

        return (
            <div className="w3-main w3-content w3-padding" style={style}>
                <video className="video-js vjs-default-skin vjs-16-9 vjs-big-play-centered" controls autoPlay muted  ref={this.player}></video>
            </div>
        );
    }
}

export default Streaming;