import HttpHandler from './APIUtils';
import { UserAPI } from './API';

export interface UserStatusMsg {
    name?: string;
    message?: string;
    success: boolean;

}

export interface User {
    account?: string;
    password?: string;
    pswRepeat?: string;
    name?: string;
    email?: string;
}

class UserService {
    private api: UserAPI;

    constructor(){
        this.api = new UserAPI()
    }

    login = (data: User):Promise<UserStatusMsg> => {
        let url = this.api.basePath + this.api.login;
        let option = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: new Headers({
                'Content-Type': 'application/json'
            })
        }
        return HttpHandler(url, option);
    }

    logout = ():Promise<UserStatusMsg> => {
        let url = this.api.basePath + this.api.logout;
        return HttpHandler(url);
    }

    isLogin = ():Promise<UserStatusMsg> => {
        let url = this.api.basePath + this.api.isLogin;
        return HttpHandler(url);
    }

    newAccount = (data: User):Promise<UserStatusMsg> => {
        let url = this.api.basePath + this.api.newAccount;
        let option = {
            method: 'POST',
            body: JSON.stringify(data),
            headers: new Headers({
                'Content-Type': 'application/json'
            })
        }
        return HttpHandler(url, option);
    }
}

export default UserService;