import React from 'react';
import VideoService, { Video } from '../api/videoService';

interface VProps {
    dash: string;
    name: string;
    coverImage: string;
}

const VideoList: React.FC<{video: VProps}> = ({video}) =>{
    let url = "/streaming?" + video.dash
    return (
        <a href={url}>
            <div className="w3-col l3 m6 w3-margin-bottom">
                <div className="w3-display-container">
                    <div className="w3-display-topleft w3-black w3-padding">{video.name}</div>
                    <img src={video.coverImage} alt="House" style={{width:"100%"}}></img>
                </div>
            </div>
        </a>
    )
};

interface HProps{}
interface HState {
    videoList: Video[]   
}
class Home extends React.Component<HProps, HState> {
    private videoService: VideoService;

    constructor(props: HProps){
        super(props);
        this.state = {
            videoList: []
        }
        this.videoService = new VideoService();
    }

    componentDidMount(){
        this.getVideoList();
    }

    getVideoList = async():Promise<void> => {
        let data = await this.videoService.getVideoList();
        this.setState({videoList: data});
    }

    render() {
        
        let renderVideoList = this.state.videoList.map((video) => {
            return <VideoList key={this.state.videoList.indexOf(video)} video={video}/>
        })
    
        return (
            <div className="home">
                <header className="w3-display-container w3-content w3-wide" style={{maxWidth:"1000px"}} id="home">
                    <img className="w3-image" src="images/title.jpg" alt="Architecture" width="1200" height="600"></img>
                </header>
                <div className="w3-content w3-padding" style={{maxWidth:"1564px"}}>
                    <div className="w3-container w3-padding-32" id="Videos">
                        <h3 className="w3-border-bottom w3-border-light-grey w3-padding-16">最新影片</h3>
                    </div>
                    <div className="w3-row-padding">
                        {renderVideoList}
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;