import React from 'react';
import './form.css'
import UserService from '../api/UserService';

interface RProps {}
interface RState {
    account?: string;
    password?: string;
    pswRepeat?: string;
    name?: string;
    email?: string;
    status?: string;
}

class Register extends React.Component<RProps, RState> {
    private userService: UserService;

    constructor(props: RProps){
        super(props);
        this.userService = new UserService();
        this.state = {
            account: '',
            password: '',
            pswRepeat: '',
            name: '',
            email:'',
            status:''
        }
    } 

    handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        e.preventDefault()
        this.setState({[e.target.name]: e.target.value}) //html tag <name="name" value="value">
    }
 
    submitInfo = async() => {
        let data: any = this.state;
        let vaildInput:number = 0;
        for(var value in data){
            if(data[value] === "" && value !== 'status')
                vaildInput = 1;
        }
        if(this.state.password !== this.state.pswRepeat)
            vaildInput = 2;
        switch (vaildInput) {
            case 1: {
                this.setState({status:'您有欄位未填!'});
                break;
            }
            case 2:{
                this.setState({status:'密碼與再次確任密碼不同!'});
                break;
            }
            default:{
                let msg = await this.userService.newAccount(data)
                if(!msg.success)
                    this.setState({status:'此帳號已經存在!'});
                else
                    window.location.href = '/login';
                break;
            }
        }
    }

    render (){
        return (
            <div className="container">
                <div className="formContainer">
                    <h1>註冊成為<b>You</b>Tobe會員</h1>
                    <hr></hr>
                    <label htmlFor="account"><b>帳號</b></label>
                    <input type="text" placeholder="帳號" name="account" value={this.state.account} onChange={this.handleChange} required></input>

                    <label htmlFor="psw"><b>密碼</b></label>
                    <input type="password" placeholder="密碼" name="password" value={this.state.password} onChange={this.handleChange} required></input>

                    <label htmlFor="psw-repeat"><b>確認密碼</b></label>
                    <input type="password" placeholder="再次輸入密碼" name="pswRepeat" value={this.state.pswRepeat} onChange={this.handleChange} required></input>

                    <label htmlFor="name"><b>使用者名稱</b></label>
                    <input type="text" placeholder="名字" name="name" value={this.state.name} onChange={this.handleChange}  required></input>

                    <label htmlFor="email"><b>Email</b></label>
                    <input type="text" placeholder="Email" name="email" value={this.state.email} onChange={this.handleChange} required></input>

                    <label style={{color:"red"}}>{this.state.status}</label>
                    <hr></hr>
                    <button onClick={this.submitInfo} className="registerbtn">註冊</button>
                </div>
                <div className="signin">
                    <p>已經是會員? <a href="/login">登入</a>.</p>
                </div>
            </div>
        );
    }
}

export default Register
