import HttpHandler from './APIUtils';
import { VideoAPI } from './API';

export interface Video {
    id?:string;
    name: string;
    coverImage: string;
    dash: string;
    describe: string;   
}

class VideoService {
    private api: VideoAPI;

    constructor(){
        this.api = new VideoAPI()
    }

    getVideoList = async():Promise<Video []> => {
        let url = this.api.basePath + this.api.getVideoList;
        let data = await HttpHandler(url);
        return data;
    }
}

export default VideoService;